body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  width: 200px;
  padding-inline: 16px;
}

.card--image {
  width: 160px;
  margin-bottom: 40px;
  padding-top: 32px;
}

.card--title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card span {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 160px;
  height: 40px;
  margin-top: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.rating__block {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.rating__list {
  gap: 4px;
  padding: 0;
  list-style: none;
  display: flex;
}

.rating__list span {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.rating__stars {
  background-image: url("star.822d561e.svg");
  background-size: contain;
  width: 16px;
  height: 16px;
}

.rating__stars:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.price__block {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price--text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price--count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
